.nav{
    position: fixed;
    top: 0;
    display: flex;
    padding: 20px;
    justify-content: space-between;
    width: 100%;
    height: 35px;
    transition: all 0.5s;
    transition-timing-function: ease-in;
}
.nav__black{
    background-color: #111;
    
}

.nav__logo{
    position: fixed;
    left: 20px;
    width: 150px;
    object-fit: contain;
}

.nav__avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}