.row{
    margin-left: 20px;
    color: #fff;
}
.row__poster{
    width: 100%;
    object-fit: contain;
    max-height: 150px;
    margin-right: 20px;
    transition: transform 450ms;
}

.row__poster:hover{
    transform: scale(1.15);
    
}
.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 25px;
}
.row__posters::-webkit-scrollbar{
    display: none;
}

.row__posterLarge{
    max-height: 250px;
    margin-right: 16px;
}

.row__posterLarge:hover{
    transform: scale(1.20);
}